.header {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	padding: rem(30) 0;
	background: linear-gradient(to bottom, rgba(#000, 0.5), rgba(#000, 0.0));
	
	@media (max-width: 767px) {
		padding: rem(15) 0;
	}
	&__cover {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	.logo {
		display: block;
		max-width: rem(172);
		width: 100%;
		flex-shrink: 0;
		margin-right: rem(45);

		@media (max-width: 991px) {
			max-width: rem(130);
		}
	}
	&__nav {
		position: relative;

		@media (max-width: 991px) {
			margin: 0 rem(20);
		}
	}

	&__nav-list {
		display: flex;
		align-items: center;
		flex-wrap: wrap;

		@media (max-width: 991px) {
			display: none;
			position: absolute;
			top: 100%;
			right: 0;
			background: linear-gradient(to top, rgba(#000, 1), rgba(#000, 0.0) 91%);
			padding: rem(25) rem(50);
			right: rem(-50);
			z-index: 10;

			&.show {
				display: block;
			}
		}

		li {
			margin: rem(0) rem(25);

			@media (max-width: 1199px) {
				margin: 0 rem(15);
			}

			@media (max-width: 991px) {
				margin: rem(10) 0;
				text-align: right;
			}

			a {
				color: #fff;
				font-size: rem(18);
				line-height: rem(21);
				text-transform: uppercase;
				padding: rem(20) 0;

				@media (max-width: 1199px) {
    			font-size: rem(16);
				}

				@media (max-width: 991px) {
					text-align: right;
				}


			}
		}

	}


	&__right-content {
		display: flex;
		align-items: center;
		justify-content: flex-end;

		@media (max-width: 991px) {}
	}
}


.user-nav {
	width: rem(65);
	height: rem(40);
	display: flex;
	align-items: center;
	justify-content: center;
	position: relative;

	&:hover {
		background-color: #fff;
		.login-ico {
			background: url("../assets/icons/login-purple.png") no-repeat 50% / contain;
		}

		.user-nav__menu {
			display: block;
		}
	}

	&__menu {
		position: absolute;
		top: 100%;
		width: rem(150);
		right: 0;
		background-color: #fff;
		display: none;
		padding: rem(5) 0;

		li {
			a {
				line-height: rem(35);
				width: 100%;
				font-size: rem(15);
				text-align: center;
				display: block;
			}
		}
	}
}



.menu-btn {
	display: none;
	width: rem(30);
	height: rem(20);
	transition: all .3s ease;
	

	@media (max-width: 991px) {
		display: block;
	}

	&.close-menu {
		.line {
			position: relative;

			&:nth-child(2) {
				opacity: 0;
			}

			&:nth-child(1) {
				transform: rotate(45deg);
				top: calc(50% - 1px);
			}

			&:nth-child(3) {
				transform: rotate(-45deg);
				bottom: calc(50% - 1px);
			}
		}
	}

	&:hover {
		opacity: 0.85;
	}
	
	.lines {
		display: block;
		width: 100%;
		height: 100%;
		display: flex;
		flex-flow: column;
		justify-content: space-between;
	}

	.line {
		display: block;
		width: 100%;
		height: rem(2);
		background: #fff;
		transition: all .2s ease;
	}
}

